.page-title {
  margin: 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  color: #000000;
  padding-right: 18px;
  padding-left: 18px;

  @media (min-width: @tablet-width) {
    font-size: 21px;
  }
}

.page-title--larger {
  @media (min-width: @tablet-width) {
    font-size: 30px;
  }
}
